.timeline button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

.timeline {
  white-space: nowrap;
  overflow-x: hidden;
}

.timeline ol {
  width: 100vw;
  padding: 250px 0;
  font-size: 0;
  transition: all 1s;
}

.timeline ol li {
  width: 160px;
  height: 3px;
  background: #222;
  list-style-type: none;
  display: inline-block;
  position: relative;
}

.timeline ol li:first-child {
  width: 40px;
}

.timeline ol li:last-child {
  width: 280px;
}

.timeline ol li:not(:first-child) {
  margin-left: 14px;
}

.timeline ol li:not(:last-child):after {
  content: "";
  width: 12px;
  height: 12px;
  background: #2a76dd;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  bottom: 0;
  left: calc(100% + 1px);
  transform: translateY(-50%);
}

.timeline ol li div {
  width: 280px;
  white-space: normal;
  color: #222;
  background: #d5d5d5;
  padding: 12px;
  font-size: 1rem;
  position: absolute;
  left: calc(100% + 7px);
}

.timeline ol li div:before {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  position: absolute;
  top: 100%;
  left: 0;
}

.timeline ol li:nth-child(2n+1) div {
  top: -16px;
  transform: translateY(-100%);
}

.timeline ol li:nth-child(2n+1) div:before {
  border-width: 8px 8px 0 0;
  border-color: #d5d5d5 #0000 #0000;
  top: 100%;
}

.timeline ol li:nth-child(2n) div {
  top: 16px;
}

.timeline ol li:nth-child(2n) div:before {
  border-width: 8px 0 0 8px;
  border-color: #0000 #0000 #0000 #d5d5d5;
  top: -8px;
}

.timeline time {
  margin-bottom: 8px;
  font-size: 1.2rem;
  font-weight: bold;
  display: block;
}

.timeline .arrows {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.timeline .arrows .arrow__prev {
  margin-right: 20px;
}

.timeline .disabled {
  opacity: .5;
}

